import React from 'react';
import { Link } from 'react-router-dom';

import footerLogin from 'assets/icons/log-in-icon.svg';
import greffLogo from 'assets/images/Greff_logo_A.svg';
import { BackTop } from 'antd';

const Footer = ({ list, menu }) => (
  <footer className="footer">
    <BackTop>
      <div className="back-top">
        <button>
          <i className="fas fa-chevron-up"></i>
        </button>
      </div>
    </BackTop>

    <div className="d-flex footer__head container-user align-items-lg-center">
      <div className="footer__logo">
        <img src={greffLogo} alt="Logo" />
      </div>

      <div className="d-flex footer__head-content">
        <FooterList list={list || []} />

        <button className="footer__login">
          <img src={footerLogin} alt="Login Icon" />
          ログインする
        </button>
      </div>
    </div>

    <FooterMenu menu={menu || []} />
    <div className="footer__copyright">
      Copyright © Ltd All Rights Reserved.
    </div>
  </footer>
);

const FooterList = ({ list }) => (
  <ul className="footer__list d-flex">
    {list.map((item) => (
      <li className="footer__list-item" key={`${item.title}${item.link}`}>
        <a className="footer__list-link" href={item.link}>
          {item.title}
        </a>
      </li>
    ))}
  </ul>
);

const FooterMenu = ({ menu }) => (
  <div className="footer__menu">
    <ul className="footer__menu-list list d-flex">
      {menu.map((item) => (
        <li className="footer__menu-item" key={`${item.title}${item.link}`}>
          <Link className="list__item-link" to={item.link}>
            {item.title}
          </Link>
        </li>
      ))}
    </ul>
  </div>
);

export default Footer;
