import Axios from 'axios';
import React, { useEffect, useState } from 'react';
export const ContextPage = React.createContext();
const PageContext = ({ children }) => {
  const [star, setStar] = useState([]);
  const [historyJob, setHistoryJob] = useState([]);

  const store = {
    stars: [star, setStar],
    historys: [historyJob, setHistoryJob],
  };

  useEffect(() => {
    if (sessionStorage.getItem('history') !== null) {
      const dataHistory = sessionStorage.getItem('history').split(',');
      setHistoryJob(dataHistory);
    }
    if (sessionStorage.getItem('star') !== null) {
      const dataStar = sessionStorage
        .getItem('star')
        .split(',')
        .map((i) => Number(i));
      setStar(dataStar);
    }
  }, []);

  return <ContextPage.Provider value={store}>{children}</ContextPage.Provider>;
};
export default PageContext;
