import React, { useEffect, useState, useContext } from 'react';
import { Link } from 'react-router-dom';
import { ContextPage } from '../../useContext';

import greffLogo from 'assets/icons/GreffLogo.svg';
import iconFavorite from 'assets/icons/star.svg';
import iconSeen from 'assets/icons/clock-icon.svg';
import iconLogin from 'assets/icons/log-in-icon.svg';

const Header = ({ menu, setShowModal }) => {
  const { stars, historys } = useContext(ContextPage);
  const [historyJob, setHistoryJob] = historys;
  const [star, setStar] = stars;
  console.log(star);
  return (
    <header className="header">
      <div className="header__head d-flex container-user">
        <div className="header__logo d-flex">
          <Link to="/">
            <img src={greffLogo} alt="" className="logo" />
          </Link>
          <p className="header__slogan">
            「1日単位で」「すぐに」「簡単に」働ける。
          </p>
        </div>
        <div className="header__action action-button d-flex">
          <div className="d-flex test2">
            <button className="action-button__favorite">
              <img
                src={iconFavorite}
                alt="Job favorite"
                className="favorite-image"
              />
              <p className="quantity_favorite">{star && star.length}</p>
              <span>キープリスト</span>
            </button>
            <button className="action-button__seen">
              <img src={iconSeen} alt="" className="seen-image" />
              <p className="quantity_seen">{historyJob && historyJob.length}</p>
              <span>閲覧履歴</span>
            </button>
          </div>
          <div className="d-flex header__button">
            <Link to="/worker/register" className="action-button__signup">
              会員登録(無料)
            </Link>
            <button
              onClick={() => setShowModal(true)}
              className="action-button__login"
            >
              <img src={iconLogin} alt="" /> ログインする
            </button>
          </div>
        </div>

        <div className="header__mobile">
          <label
            htmlFor="menu-check"
            className="header__info-bar menu hamburger"
            id="hamburger-1"
          >
            <span className="line"></span>
            <span className="line"></span>
            <span className="line"></span>
          </label>
          <input
            hidden
            type="checkbox"
            name=""
            className="menu-check"
            id="menu-check"
          />
          <label htmlFor="menu-check" className="menu-overlay"></label>
          <MenuSidebar menu={menu || []} />
        </div>
      </div>
      <MenuHeader menu={menu || []} />
    </header>
  );
};

const MenuHeader = ({ menu }) => (
  <div className="header__menu">
    <ul className="header__menu-list list d-flex">
      {menu.map((item) => (
        <li className="list__item" key={item.title}>
          <a className="list__item-link" href={item.link}>
            {item.title}
          </a>
        </li>
      ))}
    </ul>
  </div>
);

const MenuSidebar = ({ menu }) => (
  <div className="sidebar d-flex flex-column">
    <label htmlFor="menu-check" className="menu-close">
      <i className="fas fa-times"></i>
    </label>
    <ul className="sidebar__list">
      {menu.map((item) => (
        <li className="sidebar__list-item" key={item.title}>
          <a
            href={item.link}
            className="sidebar__list-link  d-flex flex-row  justify-content-between"
          >
            {item.title}
          </a>
        </li>
      ))}
    </ul>
  </div>
);

export default Header;
