import React from 'react';
import { Link } from 'react-router-dom';
import Modal from 'antd/lib/modal/Modal';
import { useForm } from 'react-hook-form';
import { ErrorMessage } from '@hookform/error-message';

import { validateText } from 'constants/validate';
import iconLine from 'assets/icons/contact-line-icon.svg';

import { postMethod } from 'api/axiosClient';
import { message } from 'antd';

const bodyStyle = {};
const style = {
  textAlign: 'center',
};

const Login = ({ showModal, setShowModal }) => {
  function handleSave(data) {
    postMethod('/auth/worker/login', { ...data, rememberMe: '0' })
      .then((res) => {
        if (res?.status === 'ok') {
          setShowModal(false);
          message.success('Login successful.');
          localStorage.setItem('currentUser', JSON.stringify(res || {}));
        } else {
          message.error(res?.msg);
        }
      })
      .catch((err) => console.log(err));
  }

  return (
    <Modal
      title="会員登録／ログイン"
      visible={showModal}
      onCancel={() => setShowModal(false)}
      footer={null}
      destroyOnClose
      maskClosable={false}
      bodyStyle={bodyStyle}
      style={style}
    >
      <div className="d-flex align-items-center flex-column">
        <LoginForm onSave={handleSave} />
        <Link to="/worker/send" className="main__phone-link">
          パスワードを忘れた方はこちら
        </Link>
        <div className="linebar">
          <span className=""></span>
          <b>または</b>
          <span className=""></span>
        </div>
        <button className="main__home-contact-line login">
          <img className="mr-2" src={iconLine} alt="Login with Line" />
          LINEで会員登録
        </button>
        <div className="mt-3 w-100 d-flex flex-column ant-modal-register">
          <p className="mb-1 mt-2">会員登録がまだの方はこちら</p>
          <Link to="/worker/register" className="main__register-submit">
            会員登録する(無料)
          </Link>
        </div>
      </div>
    </Modal>
  );
};

const LoginForm = ({ onSave }) => {
  const { register, errors, handleSubmit } = useForm();

  return (
    <form className="" onSubmit={handleSubmit(onSave)}>
      <LoginInput
        label="電話番号"
        name="email"
        placeholder="09012345678(半角)"
        errors={errors}
        validate={validateText}
        register={register}
      />
      <LoginInput
        label="パスワード"
        name="password"
        type="password"
        placeholder="パスワード"
        errors={errors}
        validate={validateText}
        register={register}
      />
      <button type="submit" className="main__home-contact-phone mt-2">
        ログインする
      </button>
    </form>
  );
};

const LoginInput = ({
  label,
  name,
  errors,
  register,
  validate,
  placeholder,
  type,
}) => (
  <div className="main__phone-form">
    <label className="main__phone-form-label mb-1">{label}</label>
    <input
      name={name}
      type={type || 'text'}
      placeholder={placeholder}
      ref={register(validate)}
      className={(errors[name] && 'error', 'main__phone-form-input mb-2')}
    />
    <ErrorMessage
      errors={errors}
      name={name}
      as="label"
      className="text-error"
    />
  </div>
);

export default Login;
