import React, { useState } from 'react';
import queryString from 'query-string';
import { useForm } from 'react-hook-form';
import { ErrorMessage } from '@hookform/error-message';
import { message } from 'antd';

import { postMethod } from 'api/axiosClient';
import { validateText } from 'constants/validate';
import Complete from './Complete';

const SetPassword = ({ location }) => {
  const [isConfirm, setIsConfirm] = useState(false);
  const [isDone, setIsDone] = useState(false);
  const [loading, setLoading] = useState(false);

  let params = queryString.parse(location?.search);

  function handleSubmit(data) {
    setLoading(true);
    postMethod('/auth/worker/confirm-password', { ...data, ...params })
      .then((res) => {
        setLoading(false);
        if (res?.status === 'ok') {
          setIsDone(true);
          message.success('Your password set it done.');
        } else {
          message.error('Error networking.');
        }
      })
      .catch(() => setLoading(false));
  }

  function handleConfirm(data) {
    postMethod('/auth/worker/confirm-code-register', data)
      .then((res) => {
        if (res?.status === 'ok') {
          setIsConfirm(true);
          message.success('Enter password bellow here.');
        } else {
          message.error(res?.msg || res?.errors?.code[0]);
        }
      })
      .catch((err) => console.log(err));
  }

  return (
    <div className="main container-user">
      {isConfirm ? (
        isDone ? (
          <Complete />
        ) : (
          <SetPasswordForm onSave={handleSubmit} loading={loading} />
        )
      ) : (
        <ConfirmCodeForm
          onSave={handleConfirm}
          loading={loading}
          defaultValue={params}
        />
      )}
    </div>
  );
};

const SetPasswordForm = ({ onSave, loading }) => {
  const { register, errors, handleSubmit, watch } = useForm();

  return (
    <form className="main__password" onSubmit={handleSubmit(onSave)}>
      <p className="main__password-title">Set password</p>
      <p className="main__password-des">
        To set password, please enter password and click button[SET PASSWORD]
      </p>

      <PasswordInput
        name="password"
        label="Password"
        type="password"
        placeholder="Password"
        register={register}
        validate={validateText}
        errors={errors}
      />

      <PasswordInput
        name="rePassword"
        label="Confirm password"
        type="password"
        placeholder="Confirm password"
        register={register}
        validate={{
          validate: (value) =>
            value === watch('password') || 'The passwords do not match.',
        }}
        errors={errors}
      />

      <button type="submit" className="main__password-submit">
        {loading ? 'Loading ...' : 'SET PASSWORD'}
      </button>
    </form>
  );
};

const ConfirmCodeForm = ({ onSave, loading, defaultValue }) => {
  const { register, errors, handleSubmit } = useForm();

  return (
    <form className="main__password" onSubmit={handleSubmit(onSave)}>
      <p className="main__password-title">Confirm Code</p>
      <p className="main__password-des">
        To confirm code, please enter email, code and click button [CONFIRM
        CODE]
      </p>

      <PasswordInput
        name="email"
        label="Email"
        type="email"
        placeholder="Email"
        register={register}
        validate={validateText}
        errors={errors}
        defaultValue={defaultValue?.email}
      />

      <PasswordInput
        name="code"
        label="Confirm code"
        type="text"
        placeholder="Confirm code"
        register={register}
        validate={validateText}
        errors={errors}
        defaultValue={defaultValue?.code}
      />

      <button type="submit" className="main__password-submit">
        {loading ? 'Loading ...' : 'CONFIRM CODE'}
      </button>
    </form>
  );
};

export const PasswordInput = ({
  errors,
  label,
  type,
  placeholder,
  name,
  register,
  validate,
  defaultValue,
}) => (
  <div className="main__password-form">
    <label className="main__password-form-label">{label}</label>
    <input
      type={type || 'text'}
      name={name}
      placeholder={placeholder}
      ref={register(validate)}
      className={(errors[name] && 'error', 'main__password-form-input')}
      defaultValue={defaultValue || ''}
    />
    <ErrorMessage
      errors={errors}
      name={name}
      as="label"
      className="text-error message"
    />
  </div>
);

export default SetPassword;
