import React, { useState } from 'react';
import { useForm } from 'react-hook-form';

import { validateText } from 'constants/validate';

import { TextInput } from '../../components/Form';
import CompleteSuccess from './Complete';
import { postMethod } from 'api/axiosClient';
import { message } from 'antd';

const ResetPassword = () => {
  const [loading, setLoading] = useState(false);
  const [isDone, setIsDone] = useState(false);

  function handleSave(data) {
    const { password, rePassword } = data;
    const information = JSON.parse(localStorage.getItem('forgot'));
    const confirmPass = {
      email: information?.email,
      code: information?.code,
      password,
      rePassword,
    };
    setLoading(true);
    postMethod('/auth/worker/confirm-new-password', confirmPass)
      .then((res) => {
        if (res?.status === 'ok') {
          setLoading(false);
          message.success(res?.msg || 'Done.');
          localStorage.removeItem('forgot');
          setIsDone(true);
        } else {
          message.error(res?.msg || 'Error Something');
          setLoading(false);
        }
      })
      .catch((err) => console.log(err));
  }

  return (
    <div className="main__reset container-user">
      {isDone ? (
        <CompleteSuccess />
      ) : (
        <>
          <p className="main__reset-title">パスワード再設定</p>
          <ForgotPasswordForm onSave={handleSave} loading={loading} />
        </>
      )}
    </div>
  );
};

export const ForgotPasswordForm = ({ onSave, loading }) => {
  const { register, errors, handleSubmit, watch } = useForm();

  return (
    <div className="main__reset-content">
      <form className="form__reset" onSubmit={handleSubmit(onSave)}>
        <div className="form__reset-title">
          <h3 className="">
            新しいパスワードを入力し、
            <br />
            「パスワードを再設定する」ボタンを押してください。
          </h3>
        </div>

        <TextInput
          name="password"
          label="新パスワード"
          type="password"
          errors={errors}
          validate={validateText}
          register={register}
        />

        <TextInput
          name="rePassword"
          label="新パスワード(確認)"
          type="password"
          errors={errors}
          validate={{
            validate: (value) =>
              value === watch('password') || 'The passwords do not match.',
          }}
          register={register}
        />

        <button className="reset-pass-button" type="submit">
          {loading ? 'Loading' : 'パスワードを再設定する'}
        </button>
      </form>
    </div>
  );
};

export default ResetPassword;
