import { getArrDay } from 'helpers';
import React, {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import Slider from 'react-slick';
import _ from 'lodash';
import { getMethod } from 'api/axiosClient';
import { debounce } from 'lodash';
import { getAll } from 'api/user.server';

const settings = {
  slidesToShow: 7,
  slidesToScroll: 7,
  speed: 1000,
  infinite: false,
};

const handleArr = (arr) =>
  arr.map((item) => `date[]=${item?.dateFormat}` || '').join('&');

const SidebarTime = ({ setData, setTotalWork }) => {
  const [date, setDate] = useState([...getArrDay()]);
  const [total, setTotal] = useState(0);
  const ref = useRef({});

  useEffect(() => {
    const dateChoose = date.filter((item) => item.check === true);
    total > 0
      ? getMethod('/search?' + handleArr(dateChoose)).then((res) => {
          setData(res?.data?.jobs?.data || []);
          setTotalWork(res?.data?.jobs?.total);
        })
      : getAll(1).then((res) => {
          setData(res?.data?.jobs?.data || []);
          setTotalWork(res?.data?.jobs?.total);
        });
  }, [total]);

  const next = () => {
    ref.current.slickNext();
  };

  const previous = () => {
    ref.current.slickPrev();
  };

  const handleClick = (data) => {
    const dateChoose = [];
    if ((data.check === true && total <= 9) || total < 9) {
      const updateDate = date.map((item) =>
        item.dateFormat === data.dateFormat
          ? { ...item, check: !item.check }
          : item
      );
      let total = _.countBy(updateDate, (item) => item.check).true || 0;
      setTotal(total);
      setDate(updateDate);
    } else {
      alert('一度に選択できる日付は10件までです.');
    }
  };

  return (
    <div className="slider">
      <button onClick={() => previous()}>Prev</button>
      {useMemo(
        () => (
          <Slider ref={ref} {...settings}>
            {date.map((item, index) => (
              <div key={item.date}>
                {index === 0 || index === 1 ? (
                  <div
                    style={{ border: '1px solid red' }}
                    onClick={() => handleClick(item)}
                  >
                    <h5>{index === 0 ? '今日' : '明日'}</h5>
                    <p>
                      {item.date.date()}月{item.date.month()} 日 水
                    </p>
                  </div>
                ) : (
                  <div
                    style={{
                      border: '1px solid red',
                      color: `${item.check ? 'blue' : ''}`,
                    }}
                    onClick={() => handleClick(item)}
                  >
                    <h5>
                      {item.date.date()}月{item.date.month()}日
                    </h5>
                    <p>{item.nameDay}</p>
                  </div>
                )}
              </div>
            ))}
          </Slider>
        ),
        [date]
      )}
      <button onClick={() => next()}>Next</button>
    </div>
  );
};
export default SidebarTime;
