import { postMethod } from 'api/axiosClient';
import { validateEmail, validateText } from 'constants/validate';
import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { TextInput } from '../../components/Form';

const SendMail = ({ history }) => {
  const [isSendMail, setIsSendMail] = useState(true);
  const [loading, setLoading] = useState(false);
  const [msg, setMsg] = useState({});
  const { register, errors, handleSubmit } = useForm();

  function onSave(data) {
    localStorage.removeItem('forgot');
    const { email, code } = data;
    setLoading(true);
    setMsg({});
    isSendMail
      ? postMethod('/auth/worker/verification-mail', { email })
          .then((res) => {
            setLoading(false);
            if (res?.status === 'ok') {
              setIsSendMail(false);
              setMsg({ success: 'Enter code below here' });
            } else {
              setMsg({ error: res?.msg || 'Error Something' });
            }
          })
          .catch(() => {
            setLoading(false);
          })
      : postMethod(`/auth/worker/confirm-code?email=${email}&code=${code}`)
          .then((res) => {
            setLoading(false);
            if (res?.msg === 'ok') {
              history.push(`/worker/forgot`);
              localStorage.setItem('forgot', JSON.stringify({ email, code }));
            } else {
              setMsg({ error: res?.msg || 'Error Code' });
            }
          })
          .catch(() => {
            setLoading(false);
          });
  }

  return (
    <div className="main__reset container-user">
      <p className="main__reset-title">パスワード再設定</p>

      <div className="main__reset-content">
        <form className="form__reset" onSubmit={handleSubmit(onSave)}>
          <div className="form__reset-title">
            <h3 className="">
              新しいパスワードを入力し、
              <br />
              「パスワードを再設定する」ボタンを押してください。
            </h3>
          </div>

          {(msg?.success || msg?.error) && (
            <label
              className={`message message-${msg.success ? 'success' : 'error'}`}
            >
              {msg.success || msg.error}
            </label>
          )}

          <TextInput
            name="email"
            label="Email"
            type="text"
            errors={errors}
            validate={validateEmail}
            register={register}
          />

          {!isSendMail && (
            <TextInput
              name="code"
              label="Code"
              type="text"
              errors={errors}
              validate={validateText}
              register={register}
            />
          )}

          <button className="reset-pass-button" type="submit">
            {loading ? 'Sending' : isSendMail ? '送信する' : 'Send Code'}
          </button>
        </form>
      </div>
    </div>
  );
};

export default SendMail;
