import React, { useState, useEffect, useContext, useRef } from 'react';
import Slider from './Slider';
import { Statistic, Row, Col } from 'antd';
import { ContextPage } from '../../../components/useContext';
import Bus from '../../../assets/icons/bus-icon.svg';
import Clock from '../../../assets/icons/clock-icon.svg';
import Smile from '../../../assets/icons/smile-icon.svg';
import Moon from '../../../assets/icons/moon-icon.svg';
import Gift from '../../../assets/icons/gift-icon.svg';
import Yellow from '../../../assets/icons/star-yellow-icon.svg';
import Salary from '../../../assets/icons/salary.svg';
import Coffe from '../../../assets/icons/coffee.svg';
import Regular from '../../../assets/icons/folder-regular.svg';
import Award from '../../../assets/icons/award.svg';
import Briefcase from '../../../assets/icons/briefcase.svg';
import Map_pin from '../../../assets/icons/map-pin.svg';
import External from '../../../assets/icons/external-link.svg';
import Alert_circle from '../../../assets/icons/alert-circle.svg';
import Star from '../../../assets/icons/star-gray-icon.svg';
import calendar from '../../../assets/icons/calendar-icon.svg';
const { Countdown } = Statistic;
const Main = ({ data, images, occupation, onhandleClick }) => {
  const deadline = new Date(data.deadline_to_apply).getTime();
  const { stars } = useContext(ContextPage);
  const [star, setStar] = stars;
  const flagStar = star.find((db) => db === data.id);
  const changeStar = () => {
    let newStar = [...star];
    let ItemStar = newStar.find((item) => item === data.id);
    if (!ItemStar) {
      ItemStar = data.id;
      newStar.push(ItemStar);
      setStar(newStar);
      sessionStorage.setItem('star', newStar);
    } else {
      const newData = newStar.filter((db) => db !== data.id);
      setStar(newData);
      sessionStorage.setItem('star', newData);
    }
  };

  return (
    <main className="detail">
      <p className="container-user link-page">
        バイトTOP &gt; 東京 &gt; 渋谷区 &gt; 求人情報(Y0059KC6)
      </p>
      <Slider images={images}></Slider>
      <div className="container-user">
        <div className="detail__main">
          <div className="detail__head">
            <div className="d-flex">
              <div className="job__main-day d-flex">
                <img src={calendar} alt="" />
                <p className="ml-2">{data.work_date}</p>
              </div>
              <div className="job__main-info">
                <div className="job__main-station d-flex">
                  <img src={Bus} alt="" />
                  <p className="ml-2">{occupation.access_address}</p>
                </div>
                <div className="job__main-time d-flex">
                  <img src={Clock} alt="" />
                  <p className="ml-2">
                    {data.working_time_start}-{data.working_time_end}
                  </p>
                </div>
              </div>
            </div>
            <div className="job__main-title">
              <p>{data.work_name}</p>
            </div>
            <div className="job__main-salary">
              <p>￥{data.salary_total}</p>
            </div>
            <div className="job__main-des">
              <p className="job__des-content">{data.title}</p>
            </div>
            <div className="job__main-icon d-flex">
              <div className="icon__button icon__button-face">
                <img src={Smile} alt="Smile icon" />
                <p>面接なし</p>
              </div>
              <div className="icon__button icon__button-moon">
                <img src={Moon} alt="Smile icon" />
                <p>残業なし</p>
              </div>
              <div className="icon__button icon__button-gift">
                <img src={Gift} alt="Smile icon" />
                <p>面接なし</p>
              </div>
              <div className="icon__button icon__button-moon">
                <img src={Moon} alt="Smile icon" />
                <p>残業なし</p>
              </div>
              <div className="icon__button icon__button-face">
                <img src={Smile} alt="Smile icon" />
                <p>面接なし</p>
              </div>
            </div>
          </div>
          <div className="detail__aply">
            <p className="detail__aply-dead">
              あと
              <span>
                <Countdown
                  value={deadline}
                  format="H 時間
                  m
                  "
                />
              </span>
              で募集締め切り
            </p>
            <div className="d-flex">
              <button className="aply-button">このお仕事を申し込む</button>
              <button
                className="d-flex keep-button"
                onClick={() => {
                  changeStar();
                }}
              >
                <img
                  src={flagStar ? Yellow : Star}
                  alt="Keep button"
                  width="20px"
                />
                <span>キープする</span>
              </button>
            </div>
          </div>
        </div>
        <hr />
        <div className="detail__info">
          <p className="detail__info-head">求人詳細</p>
          <div className="detail__info-main">
            <div className="detail__info-left">
              <div className="detail__info-item  mb-3 d-flex">
                <div className="detail__info-title d-flex">
                  <img src={Clock} alt="Work time" />
                  <p>勤務日時</p>
                </div>
                <div className="detail__info-content d-flex">
                  <p className="detail__info-day">4月3日（木）</p>
                  <p className="detail__info-hour">
                    {data.working_time_start}-{data.working_time_end}
                  </p>
                </div>
              </div>
              <div className="detail__info-item mb-3 d-flex">
                <div className="detail__info-title d-flex">
                  <img src={Salary} alt="Salary o hour" />
                  <p>時給</p>
                </div>
                <div className="detail__info-content d-flex">
                  <p className="detail__info-money">{data.salary_per_hour}円</p>
                </div>
              </div>
              <div className="detail__info-item mb-3 d-flex">
                <div className="detail__info-title d-flex">
                  <img src={Bus} alt="Work time" />
                  <p>休憩時間</p>
                </div>
                <div className="detail__info-content d-flex">
                  <p>一部支給 上限 {data.travel_fees}円まで</p>
                </div>
              </div>
              <div className="detail__info-item mb-3 d-flex">
                <div className="detail__info-title d-flex">
                  <img src={Coffe} alt="Work time" />
                  <p>業種</p>
                </div>
                <div className="detail__info-content d-flex">
                  <p>60分</p>
                </div>
              </div>
              <div className="detail__info-item mb-3 d-flex">
                <div className="detail__info-title d-flex">
                  <img src={Regular} alt="Work time" />
                  <p>勤務日時</p>
                </div>
                <div className="detail__info-content d-flex">
                  <p>販売・接客</p>
                </div>
              </div>
            </div>
            <div className="detail__info-right">
              <div className="detail__info-item mb-3 d-flex">
                <div className="detail__info-title d-flex">
                  <img src={Award} alt="Work time" />
                  <p>応募資格</p>
                </div>
                <div className="detail__info-content d-flex">
                  <p>{occupation.skill_required}</p>
                </div>
              </div>
              <div className="detail__info-item mb-3 d-flex">
                <div className="detail__info-title d-flex">
                  <img src={Briefcase} alt="Salary o hour" />
                  <p>持ち物</p>
                </div>
                <div className="detail__info-content d-flex">
                  <p className>{occupation.speciality}</p>
                </div>
              </div>
              <div className="detail__info-item mb-3 d-flex">
                <div className="detail__info-title map-title d-flex">
                  <img src={Map_pin} alt="Work time" />
                  <p>勤務地</p>
                </div>
                <div
                  className="detail__info-content detail__info-map-content d-flex"
                  onClick={() => window.open(`/map`)}
                >
                  {occupation.work_address}
                  <button className="map-button">
                    Google map
                    <img src={External} alt="" />
                  </button>
                </div>
              </div>
              <div className="detail__info-item mb-3 d-flex">
                <div
                  className="detail__info-title d-flex"
                  style={{ width: '130px' }}
                >
                  <div className="alert-img">
                    <img src={Alert_circle} alt="Salary o hour" />
                  </div>
                  <p>持ち物</p>
                </div>
                <div className="detail__info-content d-flex">
                  <p cla ssName>
                    {occupation.note}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/*Hiện ra map khi click button google map*/}
        <div className="detail__map">
          <p className="detail__info-head">勤務地</p>
          <p style={{ width: '40%' }}>{occupation.work_address}</p>
          <div id="map">
            <iframe
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d810.3998677950434!2d139.69489272280808!3d35.662238574519584!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x60188cac6fed2c59%3A0x5d7addda9cdc381!2s1-ch%C5%8Dme-2-11%20Sh%C5%8Dt%C5%8D%2C%20Shibuya%20City%2C%20Tokyo%20150-0046%2C%20Japan!5e0!3m2!1sen!2s!4v1607270105528!5m2!1sen!2s"
              style={{ border: 0 }}
              allowFullScreen
              aria-hidden="false"
              tabIndex={0}
              width={'100%'}
              height={'300px'}
              frameBorder={0}
            />
          </div>
        </div>
      </div>
      <div className="detail__main-action">
        <div className="detail__aply">
          <p className="detail__aply-dead">
            あと
            <span>
              <Countdown
                value={deadline}
                format="H 時間
                  m
                  "
              />
            </span>
            で募集締め切り
          </p>
          <div className="d-flex">
            <button className="aply-button" onClick={onhandleClick}>
              このお仕事を申し込む
            </button>
            <button
              className="d-flex keep-button"
              onClick={() => {
                changeStar();
              }}
            >
              <img
                src={flagStar ? Yellow : Star}
                alt="Keep button"
                width="20px"
              />
              <span>キープする</span>
            </button>
          </div>
        </div>
      </div>
    </main>
  );
};
export default Main;
