import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { message } from 'antd';

import { validateText } from 'constants/validate';

import { getArea } from 'api/public.services';

import { RegisterInput, RegisterSelect } from '../../components/Form';
import { postMethod } from 'api/axiosClient';

const RegisterPhone = () => {
  const [msg, setMsg] = useState({});
  const [loading, setLoading] = useState(false);

  function handleSave(data) {
    setLoading(true);
    setMsg({});
    postMethod('/auth/worker/register', { ...data, status: '1' }).then(
      (res) => {
        setLoading(false);
        if (res?.status === 'ok') {
          message.success(res?.msg);
        } else {
          setLoading(false);
          setMsg({ error: res?.msg });
          message.error(res?.msg);
        }
      }
    );
  }

  return (
    <div className="main container-user">
      <RegisterPhoneForm onSave={handleSave} msg={msg} loading={loading} />
    </div>
  );
};

const RegisterPhoneForm = ({ onSave, msg, loading }) => {
  const { register, errors, handleSubmit } = useForm();
  const [area, setArea] = useState([]);
  useEffect(() => {
    getArea()
      .then((res) => setArea(res?.areas || []))
      .catch((err) => console.log(err));
  }, []);

  return (
    <form className="main__register" onSubmit={handleSubmit(onSave)}>
      <h3 className="main__register-title">会員登録</h3>

      {(msg?.success || msg?.error) && (
        <label
          className={`message message-${msg.success ? 'success' : 'error'}`}
        >
          {msg.success || msg.error}
        </label>
      )}

      <table className="main__register-table">
        {/* Row 1 */}
        <tbody>
          <TRTable title="氏名" required>
            <div className="d-none title phonenumber">
              <span className="span">必須</span>
              <pre className="pre">氏名</pre>
            </div>
            <RegisterInput
              name="worker_first_name"
              label="姓"
              placeholder="山田"
              errors={errors}
              validate={validateText}
              register={register}
              classNameInput="input"
            />
            <RegisterInput
              name="worker_last_name"
              label="名"
              placeholder="太郎"
              errors={errors}
              validate={validateText}
              register={register}
              classNameInput="input"
            />
          </TRTable>

          {/* Row 2 */}
          <TRTable title="フリガナ" required>
            <div className="d-none title phonenumber">
              <span className="span">必須</span>
              <pre className="pre">フリガナ</pre>
            </div>
            <RegisterInput
              name="furigana_first_name"
              label="セイ"
              placeholder="ヤマダ"
              errors={errors}
              validate={validateText}
              register={register}
              classNameInput="input"
            />
            <RegisterInput
              name="furigana_last_name"
              label="メイ"
              placeholder="タロウ"
              errors={errors}
              validate={validateText}
              register={register}
              classNameInput="input"
            />
          </TRTable>

          {/* Row 5 */}
          <TRTable title="電話番号" required>
            <div className="d-none title phonenumber">
              <span className="span">必須</span>
              <pre className="pre">電話番号</pre>
            </div>
            <RegisterInput
              name="phone_number"
              placeholder="09012345678(半角)"
              className="d-flex flex-column ml-3 mr-2 mt-2 mb-2 w100"
              errors={errors}
              validate={validateText}
              register={register}
              classNameInput="input"
            />
          </TRTable>

          {/* Row 6 */}
          <TRTable title="メールアドレス" required>
            <div className="d-none title phonenumber">
              <span className="span">必須</span>
              <pre className="pre">メールアドレス</pre>
            </div>
            <RegisterInput
              name="email"
              placeholder="greff@example.jp"
              errors={errors}
              className="d-flex flex-column ml-3 mr-2 mt-2 mb-2 w100"
              validate={validateText}
              register={register}
              classNameInput="input mt-1"
            >
              <p className="p mt-2">
                *メール受信制限をされている方は、greff.jpからの受信を許可してください。
              </p>
              <p className="p">
                *入力間違いが多くなっておりますので、ご注意ください。
              </p>
            </RegisterInput>
          </TRTable>

          <TRTable title="希望勤務地" required>
            <div className="d-none title phonenumber">
              <span className="span dark">必須</span>
              <pre className="pre">希望勤務地</pre>
            </div>
            <RegisterSelect
              data={area}
              name="area"
              register={register}
              validate={validateText}
            />
          </TRTable>

          <TRTable title="プッシュ通知" required>
            <div className="d-none title phonenumber">
              <span className="span dark">必須</span>
              <pre className="pre">プッシュ通知</pre>
            </div>
            <div className="ml-3 mr-2 mt-2 mb-2 w100">
              <div className="d-flex align-items-center mb-1">
                <input
                  type="checkbox"
                  name="push_notify"
                  ref={register}
                  className="radio-box"
                />
                <p>許可する</p>
              </div>
              <pre>*新着求人やキャンペーンのお知らせがすぐに届きます！</pre>
            </div>
          </TRTable>
        </tbody>
      </table>

      <a href="/" className="main__register-link">
        利用規約とプライバシーポリシーに
      </a>

      <button className="main__register-submit">
        {loading ? 'Loading...' : '同意して会員登録する'}
        <span>▶</span>
      </button>
    </form>
  );
};

const TRTable = ({ title, required, children }) => (
  <tr className="d-flex">
    <td className="main__register-table-left">
      <p>{title}</p>
      {required && <span>必須</span>}
    </td>
    <td className="main__register-table-right">{children}</td>
  </tr>
);

export default RegisterPhone;
