import axiosClient from './axiosClient';

// Occupation
export const getAll = async (page) =>
  await axiosClient
    .get(`/jobOnTOP?page=${page}`)
    .then((response) => response?.data)
    .catch((error) => error.response);

export const createOccupation = async (data) =>
  await axiosClient.post('/occupation', data);

export const editOccupation = async (data) =>
  await axiosClient.put('/occupation/' + data?.id || 1, data);

// Jobs

export const getID = async (id) =>
  await axiosClient
    .get(`/detailsJob/${id}`)
    .then((res) => res?.data)
    .catch(() => console.error('Error Networking'));

export const filterJobs = async (page, limit, keyword, filter) =>
  await axiosClient
    .get('/jobs/store')
    .then((res) => res?.data?.listjobs)
    .catch(() => console.error('Error Networking'));

export const createJob = async (data) => {
  return await axiosClient
    .post('/jobs/store', data)
    .catch((error) => console.log(error.response));
};

export const editJob = async (data) => {
  const { id, ...rest } = data;
  return await axiosClient
    .put('/jobs/store/' + data.id, rest)
    .catch(() => console.error('Error Networking'));
};

