import dayjs from 'dayjs';

export const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));
export const subName = (obj) => '' + obj?.first + ' ' + obj?.last;

export const getArrDay = () => {
  let today = new Date();
  const day_length = 1000 * 60 * 60 * 24;
  let days = [];
  for (let i = 0; i < 60; i++) {
    let nextDay = dayjs(today.getTime() + i * day_length);
    let dayOfWeek = nextDay.day();
    days.push({
      dateFormat: nextDay.format('YYYY-MM-DD'),
      date: nextDay,
      check: false,
      dayOfWeek,
      nameDay: getDayOfWeek(dayOfWeek),
    });
  }
  return days;
};

export const getDayOfWeek = (index) => {
  let dayOfWeek = ['日', '月', '火', '水', '木', '金', '土'];
  return dayOfWeek[index];
};
