import React, { useState } from 'react';
import Header from './users/Header';
import Footer from './users/Footer';
import Login from 'pages/users/Login';

const HeaderMenu = [
  { title: 'Forgot Password', link: '/worker/send' },
  { title: 'Register User', link: '/worker/register' },
  { title: 'はじめての方へ', link: 'https://greff.co.jp/lp/business/' },
  { title: 'ヘルプ', link: 'https://greff.co.jp/faq' },
  { title: 'お問合せ', link: 'https://greff.co.jp/lp/contact/' },
];

const FooterList = [
  { title: 'はじめての方へ', link: 'https://greff.co.jp/lp/worker/' },
  { title: 'はじめての方へ', link: 'https://greff.co.jp/lp/business/' },
  { title: 'ヘルプ', link: 'https://greff.co.jp/faq' },
  { title: 'お問合せ', link: 'https://greff.co.jp/lp/contact/' },
];

const FooterMenu = [
  { title: '運営会社', link: '/' },
  { title: '運営会', link: '/' },
  { title: 'プライバシーポリシー', link: '/' },
  { title: '求人掲載について', link: '/' },
];

const LayoutUser = ({ children }) => {
  const [showModal, setShowModal] = useState(false);

  return (
    <>
      <Header menu={HeaderMenu} setShowModal={setShowModal} />
      {children}
      <Login showModal={showModal} setShowModal={setShowModal} />
      <Footer list={FooterList} menu={FooterMenu} />
    </>
  );
};

export default LayoutUser;
