import React from 'react';
import { useHistory } from 'react-router-dom';
const ClientForm = () => {
  console.log('a');

  const toLogin = (e) => {
    e.preventDefault();
  };
  const toRegister = (e) => {
    e.preventDefault();
  };
  return (
    <div>
      Bạn cần login để ứng tuyển tuyển dụng.{' '}
      <a style={{ color: 'blue' }} onClick={(e) => toLogin()} href="">
        Login
      </a>{' '}
      Ngay bây giờ
      <hr />
      Nếu chưa có tài khoản hay .{' '}
      <a onClick={(e) => toRegister()} style={{ color: 'blue' }} href="">
        Đăng ký hội viên
      </a>
    </div>
  );
};
export default ClientForm;
