export const PASSWORD_VALIDATE = /((?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[\W]).{8,64})/;
export const EMAIL_VALIDATE = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;

export const validateEmail = {
  required: 'This field is required!',
  pattern: {
    value: EMAIL_VALIDATE,
    message: 'Invalid email address',
  },
};

export const validateText = {
  required: 'This field is required!',
};

export const validatePassword = {
  pattern: {
    value: PASSWORD_VALIDATE,
    message:
      'Ensure that password is 8 to 64 characters long and contains a mix of upper and lower case characters, one numeric and one special character',
  },
  required: 'This field is required!',
};

export const validateConfirmPass = (value, watch) =>
  value === watch || 'The passwords do not match';
