import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import LayoutWorker from 'components/layout/LayoutWorker';
import LayoutUser from 'components/layout/LayoutUser';
import JobList from 'pages/user/index';
import JobDetail from 'pages/user/details';
import Register from 'pages/worker/auth/register/Register';
import ResetPassword from 'pages/worker/auth/forgot/ResetPassword';
import SendMail from 'pages/worker/auth/forgot/SendMail';
import RegisterPhone from 'pages/worker/auth/register/RegisterPhone';
import SetPassword from 'pages/worker/auth/register/SetPassword';
import Map from 'pages/user/components/Map';

const Routes = () => {
  return (
    <Router>
      <Switch>
        <Route path="/worker/:path?" exact>
          <LayoutWorker>
            <Route path="/worker/forgot" exact component={ResetPassword} />
            <Route path="/worker/send" exact component={SendMail} />
            <Route path="/worker/register" exact component={Register} />
            <Route
              path="/worker/register-phone"
              exact
              component={RegisterPhone}
            />
            <Route
              path="/worker/confirm-code-register"
              exact
              component={SetPassword}
            />
          </LayoutWorker>
        </Route>
        <Route>
          <LayoutUser>
            <Route path="/" exact component={JobList} />
            <Route path="/details/:path" exact component={JobDetail} />
            <Route path="/map" exact component={Map} />
          </LayoutUser>
        </Route>
      </Switch>
    </Router>
  );
};
export default Routes;
