import { ErrorMessage } from '@hookform/error-message';

export const TextInput = ({
  errors,
  label,
  name,
  required,
  register,
  validate,
  type,
}) => {
  return (
    <div className="form__reset-input d-flex flex-column">
      <label>
        {label} {required && <span>*</span>}
      </label>
      <input
        type={type || 'text'}
        name={name}
        placeholder={name}
        ref={register(validate)}
        className={errors[name] && 'error'}
        defaultValue=""
      />
      <ErrorMessage
        errors={errors}
        name={name}
        as="label"
        className="text-error"
      />
    </div>
  );
};

export const RegisterInput = ({
  errors,
  label,
  name,
  register,
  validate,
  placeholder,
  children,
  className,
  classNameInput,
  type,
}) => (
  <div className={className || 'd-flex flex-column ml-3 mr-2 mt-2 mb-2 w45'}>
    {label && <p>{label}</p>}
    <input
      name={name}
      type={type || 'text'}
      placeholder={placeholder}
      ref={register(validate)}
      className={(errors[name] && 'error', classNameInput)}
      defaultValue=""
    />
    <ErrorMessage
      errors={errors}
      name={name}
      as="label"
      className="text-error"
    />
    {children}
  </div>
);

export const RegisterSelect = ({
  data,
  name,
  register,
  validate,
  defaultValue,
}) => (
  <div className="m-2">
    <select
      name={name}
      ref={register(validate)}
      className="js-example-disabled-results"
    >
      {data.map((item) => (
        <option key={item.id} value={item.id}>
          {item?.name || ''} {item?.kanji_name || ''}
        </option>
      ))}
    </select>
  </div>
);
