import React from 'react';
import Calendar from '../../../assets/icons/calendar-icon.svg';
import Bus from '../../../assets/icons/bus-icon.svg';
import Clock from '../../../assets/icons/clock-icon.svg';
import Smile from '../../../assets/icons/smile-icon.svg';
import { URL_IMAGES } from '../../../constants/urlImage';
import Yellow from '../../../assets/icons/star-yellow-icon.svg';
import Moon from '../../../assets/icons/moon-icon.svg';
import Gift from '../../../assets/icons/gift-icon.svg';
import Star from '../../../assets/icons/star-gray-icon.svg';
const JobItem = ({
  data,
  sessionStar,
  occupation,
  images,
  getToDetails,
  getHistory,
  getStar,
}) => {
  const Data = sessionStar.find((db) => Number(db) === data.id);
  return (
    <div className="main__job-item job">
      <div className="job__image">
        <img
          // src={`${URL_IMAGES}/${images['0'] ? images['0'].url : ''}`}
          src="https://i.picsum.photos/id/948/536/354.jpg?hmac=8PZMUP3rZls4hlGwM8-LUNUdeg3ceA1Daz82XI-lAyY"
          alt=""
        />
      </div>
      <div className="job__main">
        <div className="job__main-day d-flex">
          <img src={Calendar} alt="" />
          <p className="ml-2">{data.work_date}</p>
        </div>
        <div className="job__main-info">
          <div className="job__main-station d-flex">
            <img src={Bus} alt="" />
            <p className="ml-2">{occupation.work_address}</p>
          </div>
          <div className="job__main-time d-flex">
            <img src={Clock} alt="" />
            <p>
              {data.working_time_start}-{data.working_time_end}
            </p>
          </div>
        </div>
        <div className="job__main-title">
          <p>{data.title}</p>
        </div>
        <div className="job__main-salary">
          <p>{data.salary_per_hour}</p>
        </div>
        <div className="job__main-icon d-flex">
          <div className="icon__button icon__button-face">
            <img src={Smile} alt="Smile icon" />
            <p>面接なし</p>
          </div>
          <div className="icon__button icon__button-moon">
            <img src={Moon} alt="Smile icon" />
            <p>残業なし</p>
          </div>
          <div className="icon__button icon__button-gift">
            <img src={Gift} alt="Smile icon" />
            <p>面接なし</p>
          </div>
        </div>
      </div>
      <hr />
      <div className="job__action d-flex pt-4">
        <div className="job__action-detail">
          <button
            className="detail-button"
            onClick={() => {
              getToDetails(data.id);
              getHistory(data.id);
            }}
          >
            詳細を見る
            <span> ▶</span>
          </button>
        </div>
        <div className="job__action-keep">
          <button
            className="d-flex keep-button"
            onClick={() => {
              getStar(data.id);
            }}
          >
            <img src={Data ? Yellow : Star} alt="Keep button" width="20px" />
            <span>キープする</span>
          </button>
        </div>
      </div>
    </div>
  );
};
export default JobItem;
