import React, { useEffect, useState } from 'react';
import Main from './components/Main';
import ClientForm from './components/ClientForm';
import UserFrom from './components/useForm';
import Modal from './components/Modal';
import useModal from './components/UseModal';
import { getID } from 'api/user.server';

const JobDetail = (props) => {
  const b = 1;
  const [a, setA] = useState(0);
  const [dataJob, setDataJob] = useState([]);
  const [images, setImages] = useState([]);
  const { isShowing, toggle } = useModal();
  const [occupation, setOccupation] = useState([]);
  console.log(dataJob);

  const onhandleCheck = () => {
    if (b === 1) {
      toggle();
      setA(1);
    } else if (b === 2) {
      toggle();
      setA(2);
    } else {
    }
  };

  const params = Number(props.match.params.path);
  useEffect(() => {
    const getData = () => {
      getID(params).then((res) => {
        setDataJob(res?.detailsJob[0]);
        setImages(res?.detailsJob[0]?.occupation.images);
        setOccupation(res?.detailsJob[0]?.occupation);
      });
    };
    window.scroll(0, 0);
    getData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div>
      <Main
        occupation={occupation}
        onhandleClick={onhandleCheck}
        images={images}
        data={dataJob}
      ></Main>
      {a === 1 && (
        <Modal isShowing={isShowing} hide={toggle}>
          <ClientForm closeModal={() => toggle()} />
        </Modal>
      )}
      {a === 2 && (
        <Modal isShowing={isShowing} hide={toggle}>
          <UserFrom closeModal={() => toggle()} />
        </Modal>
      )}
    </div>
  );
};
export default JobDetail;
