import React, { useEffect, useContext, useState } from 'react';
import { ContextPage } from '../../components/useContext';
import { getAll } from 'api/user.server';
import JobItem from './components/items';
import { useHistory } from 'react-router-dom';
import Icon from 'assets/icons/crosshairs-solid-icon.svg';
import SidebarTime from './SidebarTime';

const JobList = () => {
  const [Job, setJob] = useState([]);
  const { stars, historys } = useContext(ContextPage);
  const [historyJob, setHistoryJob] = historys;
  const [star, setStar] = stars;
  const [lastPage, setLastPage] = useState();
  const [currentPage, setCurrentPage] = useState(1);
  const history = useHistory();
  const [total, setTotal] = useState(0);

  const getToDetails = (e) => history.push(`details/${e}`);
  const getHistory = (e) => {
    let newStar = [...historyJob];
    let ItemStar = newStar.find((item) => item === `/job-details/${e}`);
    if (!ItemStar) {
      ItemStar = `/job-details/${e}`;
      newStar.push(ItemStar);
      setHistoryJob(newStar);
      sessionStorage.setItem('history', newStar);
    } else {
    }
  };

  const readMore = () => {
    let Array = [...Job];
    setCurrentPage(currentPage + 1);
    if (currentPage <= lastPage) {
      getAll(currentPage + 1).then((res) => {
        res.data.jobs.data.map((db) => {
          Array.push(db);
        });
        setJob(Array);
      });
    } else {
      window.alert('hết rồi');
    }
  };

  const getStar = (e) => {
    let newStar = [...star];
    let ItemStar = newStar.find((item) => item === e);
    if (!ItemStar) {
      ItemStar = e;
      newStar.push(ItemStar);
      setStar(newStar);
      sessionStorage.setItem('star', newStar);
    } else {
      const newData = newStar.filter((db) => db !== e);
      sessionStorage.setItem('star', newData);
      if (!newData) {
        sessionStorage.clear('star');
      }
      setStar(newData);
    }
  };

  useEffect(() => {
    const getlist = () => {
      getAll(1).then((res) => {
        setLastPage(res?.data?.jobs?.last_page);
        setJob(res?.data?.jobs?.data || []);
        setTotal(res?.data?.jobs?.total);
      });
    };
    getlist();
  }, []);

  return (
    <main className="main">
      {/* Slick */}
      <SidebarTime setData={setJob} setTotalWork={setTotal} />

      {/*Main condition*/}
      <div className="main__condition container-user">
        <div className="main__condition-head ">
          <p className="condition-day mr-2">4月3日（金）, 4月6日（月)</p>
          <span>のお仕事</span>
          <p className="condition-result ml-3">
            {total} <span>件</span>
          </p>
        </div>
        <div className="main__condition-search">
          <button className="condition-map mr-2">
            <img src={Icon} alt="" className="mr-2" />
            現在地から絞る
          </button>
          <button className="condition-station mr-2">
            <span>▼</span> 駅名から絞る
          </button>
          <button className="condition-career">
            <span>▼</span>業種から絞る
          </button>
        </div>
        <div className="main__condition-sort">
          <button className="sort sort-day sort-selected">日付順</button>
          <button className="sort sort-new">新着順</button>
          <button className="sort sort-salary">給与順</button>
        </div>
      </div>
      {/*Main Job*/}
      <div className="main__job container-user d-flex">
        {Job &&
          Job.map((db, index) => {
            return (
              <JobItem
                key={index}
                data={db}
                sessionStar={star}
                images={db.occupation.images}
                occupation={db.occupation}
                getHistory={getHistory}
                getStar={getStar}
                getToDetails={getToDetails}
              />
            );
          })}
      </div>
      <div className="main__show-more">
        <button
          className="more-job"
          onClick={() => {
            readMore();
          }}
        >
          もっと見る
        </button>
      </div>
    </main>
  );
};
export default JobList;
